/** @format */

import { Component, OnInit } from '@angular/core';

import { Chart } from 'chart.js';

import { InvoiceData, InvoiceStatus } from '../../_models/Invoice';
import { Type } from '../../_models/Chart';
import { InvoiceService } from 'src/app/_services/invoice.service';

@Component({
  selector: 'app-chart',
  templateUrl: './chart.component.html',
  styleUrls: ['./chart.component.css']
})
export class ChartComponent implements OnInit {
  data: InvoiceData[] = [];
  chart: [] = [];

  constructor(private _invoice: InvoiceService) {}

  ngOnInit(): void {
    this._invoice.getData().subscribe(
      res => {
        this.data = res;
        this.createChart(Type.Line, 'test');
      },
      error => alert(error.message)
    );
  }

  createChart(type: string, name: string): void {
    this.chart = new Chart('canvas', {
      type: type,
      data: {
        labels: this.data.map(x => new Date(x.Date).toDateString()),
        datasets: [
          {
            label: name,
            backgroundColor: 'rgb(255, 255, 25)',
            borderColor: 'rgb(255, 99, 132)',
            data: this.data.map(x => x.Amount),
            fill: false
          }
        ]
      },
      options: {
        layout: {
          padding: {
            left: 50,
            right: 0,
            top: 0,
            bottom: 0
          }
        }
      }
    });
  }
}
